.intro-line {
  height: 80px;
  width: 100%;
  background-color: rgb(31, 31, 53);
}

.intro-info-block {
  min-height: 500px;
  
}

.intro-container{
  background-image: linear-gradient(75deg, #191726 45%,  rgb(31, 31, 53) 45%);
}

.intro-info-block {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  color: #F2F1FF;
}